import { Box, Button, Grid, Typography } from "@material-ui/core";
import { CSSProperties } from "@material-ui/core/styles/withStyles";
import { navigate, RouteComponentProps } from "@reach/router";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";
import NiceBox from "../common/NiceBox";

interface ILoginPage extends RouteComponentProps {}

export const LoginPromptPage = (props: ILoginPage) => {
  const boxStyle: CSSProperties = {
    textAlign: "center",
  };
  const headStyle: CSSProperties = {
    // textAlign: "center",
  };

  const returnUrl = useStoreState((s) => s.returnUrl);
  const setReturnUrl = useStoreActions((a) => a.returnUrl.setItem);
  const hVariant = "h5";
  const niceBoxP = 6;
  const line2My = 3;

  return (
    <Box maxWidth={960} mx="auto" mt={7} mb={12}>
      <Grid container spacing={2}>
        <Grid item xs={12} md={6}>
          <NiceBox style={boxStyle} p={niceBoxP}>
            <Typography variant={hVariant} style={headStyle}>
              New User
            </Typography>
            <Box my={line2My}>
              <Typography>
                An account will be created for you automatically
              </Typography>
            </Box>
            <Box m={2}>
              <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  navigate(returnUrl.item ? returnUrl.item : "/");
                  setReturnUrl(undefined);
                }}
              >
                Continue
              </Button>
            </Box>
          </NiceBox>
        </Grid>
        <Grid item xs={12} md={6}>
          <NiceBox style={boxStyle} p={niceBoxP}>
            <Typography variant={hVariant} style={headStyle}>
              Existing User
            </Typography>
            <Box my={line2My}>
              <Typography>
                If you have an Estreeh account, please login here:
              </Typography>
            </Box>
            <Box m={2}>
              <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  navigate("/login");
                }}
              >
                Login
              </Button>
            </Box>
          </NiceBox>
        </Grid>
      </Grid>
    </Box>
  );
};
