import { Box, Button, Grid, TextField, Typography } from "@material-ui/core";
import axios, { AxiosResponse } from "axios";
import React, { ReactElement, useState } from "react";
import { IApiRegisterUserBody } from "../../../../estreeh-common-library/build/interfaces/IApiRegisterUserBody";
import { IResponse } from "../../../../estreeh-common-library/build/interfaces/IResponse";
import { IUser } from "../../../../estreeh-common-library/build/interfaces/IUser";
import { IApiRequestCreatedResponse } from "../../../../estreeh-common-library/build/interfaces/IApiRequestCreatedResponse";
import NiceBox from "../common/NiceBox";
import check from "../../images/check.svg";
import Faker from "faker";
import { useStoreActions } from "../../helpers/StoreTypedHooks";

interface Props {
  response?: IApiRequestCreatedResponse;
}

export default function OneClickRegistration(props: Props): ReactElement {
  const [email, setEmail] = useState(props.response?.email !== undefined ? props.response.email : "" );
  const [success, setSuccess] = useState(false);
  const [inProgress, setInProgress] = useState(false);
  const loadUser = useStoreActions((action) => action.loadUser);
  // const password = Faker.random.alphaNumeric(6);
  const [password] = useState(Faker.random.alphaNumeric(6));
  const response = props.response;

  if (response !== undefined && response.fullName !== undefined) {
    const onClick = () => {
      if (response.fullName) {
        setInProgress(true);
        const body: IApiRegisterUserBody = {
          email,
          fullName: response.fullName,
          mobile: response.mobile,
          password,
          trackingCode: response.trackingCode,
        };
        axios
          .post<IApiRegisterUserBody, AxiosResponse<IResponse<IUser>>>(
            process.env.REACT_APP_API_ROOT + "/auth/register",
            body,
            { withCredentials: true }
          )
          .then((response) => {
            loadUser().finally(() => {
              setSuccess(true);
            });
          })
          .catch((reason) => {
            console.log(reason);
          })
          .finally(() => {
            setInProgress(false);
          });
      }
    };

    return (
      <Box maxWidth={440} mx="auto" my={3}>
        <NiceBox>
          {success ? (
            <>
              <Box>
                <Box my={2}>
                  <img
                    alt="success"
                    src={check}
                    style={{ display: "block", margin: "auto", maxWidth: 60 }}
                  />
                </Box>
                <Box my={2}>Successfully registered and logged in !</Box>
                <Box p={2} textAlign="initial">
                  <Grid container spacing={2}>
                    <Grid item xs={5}>
                      Username
                    </Grid>
                    <Grid item xs={7}>
                      {email}
                    </Grid>
                    <Grid item xs={5}>
                      Password
                    </Grid>
                    <Grid item xs={7}>
                      {password}
                    </Grid>
                  </Grid>
                </Box>
              </Box>
            </>
          ) : (
            <>
              <Box>
                <Typography variant="h5">Instant account</Typography>
              </Box>
              <Box>
                <Typography>Create a new account in just 1-click </Typography>
              </Box>
              <Box py={2}>
                <TextField
                  variant="outlined"
                  name="email"
                  size="small"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  disabled={inProgress}
                />
              </Box>
              <Box>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={onClick}
                  disabled={inProgress}
                >
                  {inProgress ? "Creating " : "Create my account!"}
                </Button>
              </Box>
            </>
          )}
        </NiceBox>
      </Box>
    );
  }
  return <></>;
}
