import { StoreProvider } from "easy-peasy";
import React from "react";
import MasterTheme from "./components/layouts/MasterTheme";
import { store } from "./helpers/Store";
// import logo from './logo.svg';
// import './App.css';

function App() {
  return (
    <StoreProvider store={store}>
      <div className="App" id="app-root" style={{ height: "100%" }}>
        <MasterTheme useRtl={false} />
      </div>
    </StoreProvider>
  );
}

export default App;
