import {
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  Grid,
  Slide,
  Snackbar,
  TextField,
  Typography,
} from "@material-ui/core";
import { Link, navigate, RouteComponentProps } from "@reach/router";
import axios from "axios";
import React, { useState } from "react";
import { IResponse } from "../../../../estreeh-common-library/build/interfaces/IResponse";
import Branding from "../../config/Branding";
import SmallText from "../common/SmallText";
import { Alert } from "@material-ui/lab";
import { TransitionProps } from "@material-ui/core/transitions";
import { CSSProperties } from "@material-ui/core/styles/withStyles";
import { IApiLoginBody } from "../../../../estreeh-common-library/build/interfaces/IApiLoginBody";
import { useStoreActions } from "../../helpers/StoreTypedHooks";
// import SignInWithApple from "../mock/SignInWithApple";
// import SignInWithGoogle from "../mock/SignInWithGoogle";

interface Params {
  demoLink: string;
}

interface Props extends RouteComponentProps<Params> {
}

const validateEmail = (email: string): boolean => {
  const re =
    /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
};

function SlideTransition(props: TransitionProps) {
  return <Slide {...props} direction="down" timeout={500} />;
}

//TODO: FUTURE: Rework. create "Login Modal" component that functions fully

export const LoginPage = (props: Props) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [dialogueMessage, setDialogueMessage] = useState("");
  const [dialogueShow, setDialogueShow] = useState(false);
  const [gotError, setGotError] = useState(false);
  const [attempts, setAttempts] = useState(0);
  const [inProgress, setInProgress] = useState(false);
  const loadUser = useStoreActions((action) => action.loadUser);
  // const returnUrl = useStoreState((state) => state.returnUrl);
  const setReturnUrl = useStoreActions((action) => action.returnUrl.setItem);

  const demoLink = props.demoLink ? props.demoLink : "";
  // const newReturnUrl =
  //   returnUrl.item !== undefined
  //     ? returnUrl.item
  //     : "/";

  const loginButtonDisabled =
    inProgress || password.length < 1 || !validateEmail(email);

  const onTextBoxKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter") {
      onLoginButtonClick();
    }
    setGotError(false);
  };

  const onLoginButtonClick = () => {
    if (!loginButtonDisabled) {
      setInProgress(true);
      const postBody: IApiLoginBody = {
        email,
        password,
      };
      axios
        .post<IResponse>(
          process.env.REACT_APP_API_ROOT + "/auth/login",
          postBody,
          { withCredentials: true }
        )
        .then((response) => {
          if (response.data.success) {
            loadUser().finally(() => {
              setGotError(false);
              setReturnUrl(undefined);
              // navigate(newReturnUrl);
              navigate(-1);
            });
          } else {
            loginFailed();
          }
        })
        .catch((reason) => {
          loginFailed();
        })
        .finally(() => {
          setInProgress(false);
        });
    }
  };

  const loginFailed = () => {
    //TODO: Use dialog (or better UI) instead of snackbar
    const a = attempts + 1;
    setAttempts((prev) => a);
    setGotError(true);
    const extraMsg = a > 1 ? ` (Attempt #${a}).` : `.`;
    const msg = "Wrong username or password" + extraMsg;
    console.log(msg);
    setPassword("");
    setDialogueMessage(msg);
    setDialogueShow(true);
  };

  const dialogueOnClose = () => {
    setDialogueShow(false);
  };

  const textBoxStyle: CSSProperties = {
    backgroundColor: gotError ? "#ffeeee" : "#ffffff",
  };

  //TODO: Login with Google

  return (
    <>
      <Snackbar
        TransitionComponent={SlideTransition}
        open={dialogueShow}
        autoHideDuration={4000}
        onClose={dialogueOnClose}
        anchorOrigin={{
          horizontal: "center",
          vertical: "top",
        }}
      >
        <Alert severity="error">{dialogueMessage}</Alert>
      </Snackbar>
      <Box p={1} maxWidth={640} mx="auto">
        <Box
          my={2}
          py={3}
          borderRadius={Branding.borderRadius.niceRadius}
          boxShadow={Branding.shadow.niceShadow}
          bgcolor="white"
        >
          <Box maxWidth={346} mx="auto" px={2}>
            <Box textAlign="center">
              <Typography variant="h6">Login</Typography>
            </Box>
            <Link to={demoLink} style={{ textDecoration: "none" }}>
              {/* <SignInWithGoogle />
          <SignInWithApple /> */}
            </Link>
            {/* <Box textAlign="center" px={1}>
            <Typography>OR</Typography>
          </Box> */}
            <Box py={3}>
              <form noValidate autoComplete="off">
                <TextField
                  label="Email"
                  variant="outlined"
                  value={email}
                  onChange={(event) => {
                    setEmail(event.target.value);
                  }}
                  style={textBoxStyle}
                  onKeyPress={onTextBoxKeyPress}
                  disabled={inProgress}
                />
                <TextField
                  label="Password"
                  type="password"
                  autoComplete="current-password"
                  variant="outlined"
                  value={password}
                  onChange={(event) => {
                    setPassword(event.target.value);
                  }}
                  style={textBoxStyle}
                  onKeyPress={onTextBoxKeyPress}
                  disabled={inProgress}
                />
                <Box py={1}>
                  <Grid container alignItems="center">
                    <Grid item xs={8}>
                      <Box>
                        <FormControlLabel
                          control={<Checkbox color="primary" size="small" />}
                          label={<SmallText>Keep Me Logged In</SmallText>}
                        />
                      </Box>
                    </Grid>
                    <Grid item xs={4}>
                      <Box textAlign="right">
                        <SmallText>
                          <Link to="/forgot-password">Forgot password?</Link>
                        </SmallText>
                      </Box>
                    </Grid>
                  </Grid>
                </Box>
              </form>
              <Box py={1}>
                <Button
                  color="primary"
                  variant="contained"
                  fullWidth={true}
                  onClick={onLoginButtonClick}
                  disabled={loginButtonDisabled}
                >
                  {inProgress ? "Please Wait..." : "LOGIN"}
                </Button>
              </Box>
            </Box>
            <Box mt={2} textAlign="center">
              <Typography>
                Don't have an account?
                <br /> <Link to="/signup">Click to sign-up</Link>
              </Typography>
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};
