import FormatCurrency from "../helpers/FormatCurrency";
import IWorkerTwoYear from "../../../estreeh-common-library/build/interfaces/IWorkerTwoYear";
import { TranslationPack } from "../../../estreeh-common-library/build/types/TranslationPack";

export function TransformTwoYearWorker(
  worker: IWorkerTwoYear,
  tPack: TranslationPack
) {
  let grandTotalYears = 0;
  let currency = tPack.t("currency.bhd");

  worker.jobExperiences.forEach((job) => {
    job.toYear = job.fromYear + job.totalYears;
    grandTotalYears += job.totalYears;
  });
  worker.experienceYears = grandTotalYears;
  worker.monthlySalaryBhdFormatted =
    currency + " " + FormatCurrency(worker.monthlySalary, 0) + " / Month";
  worker.processFeesBhdFormatted =
    currency + " " + FormatCurrency(worker.processFees, 0);

  // if (agency.nationalities) {
  //   worker.nationality.title = Faker.random.arrayElement(
  //     agency.nationalities
  //   ).title;
  // let demoId = getDemoId(worker.nationality.title);
  // worker.smallPicture = getDemoPic(demoId, worker.nationality.title, "Small");
  // worker.bigPicture = getDemoPic(demoId, worker.nationality.title, "Full");
  // }
}
